.ListSidebar ul {
  list-style: none;
  padding: 0;
}

.ListSidebar li,
.ListSidebar p {
  margin-bottom: 0.5rem;
}

.ListSidebar a {
  color: #444;
  display: block;
  overflow: hidden;
  text-decoration: none;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}

.ListSidebar ul > li > ul {
  padding-left: 1rem;
}



